<template>
  <div class="container">
    <div class="top">
      <navbar></navbar>
    </div>
    <div class="middle">
      <!-- uncomment for a sidebar to always be there -->
      <!--<div class="middle-sidebar">
        <sidebar></sidebar>
      </div>-->
      <div class="middle-content">
        <router-view></router-view>
      </div>
    </div>
    <div class="bottom">
      &copy; 2018 Joe Santoru
    </div>
  </div>
</template>

<script>
import Navbar from './components/Navbar'
import Sidebar from './components/Sidebar'
export default {
  name: 'App',
  components: {
    'navbar': Navbar,
    'sidebar': Sidebar
  }
}
</script>

<style>
  body {
    margin: 0; /* puts the body all the way to the edge, avoids an outer scrollbar */
  }

  html, body, .container {
    height: 100%; /* fill up the available space */
  }

  .container {
    display: flex; /* needed at the parent level, so child divs can use 'flex: n' */
    flex-direction: column; /* stack divs vertically - this allows the top bar to be on top instead of on the left */

    /* override bootstrap style to allow stretching the entire width of the screen*/
    max-width:none;
    padding:0px;
  }

  .top {
    height: 70px; /* static height nav */
    /*flex:1;*/ /* relative size to divs at the same level */
  }

  .middle {
    display: flex; /* needed at the parent level, so child divs can use 'flex: n' */
    flex: 9; /* relative size to divs at the same level */
  }

  .middle-sidebar {
    flex: 1; /* relative size to divs at the same level */
    overflow: auto; /* enable scroll bar */
    min-width:200px;
  }

  .middle-content {
    flex: 4; /* relative size to divs at the same level */
    overflow: auto; /* vertical scrollbar */
  }

  .bottom {
    height:20px;
    /*flex:.25; /* relative size to divs at the same level */
    font-size:12px;
  }
</style>



