<template>
  <div id="sidebar">
    <h2>{{msg}}</h2>
    <ul class="list-group">
      <li
        v-for="(item, index) in listItems"
        class="list-group-item"
        :class="{active:shouldBeActive(index)}"
        @click="setSelected(index)"
      >{{item}}
      </li>
    </ul>
  </div>
</template>

<script>
  export default {
    name: 'Sidebar',

    data () {
      return {
        msg: 'Sidebar',
        listItems: ['My Drive', 'Computers', 'Shared with me', 'Recent', 'Google Photos', 'Starred', 'Trash', 'Backups']
      }
    },

    methods: {
      shouldBeActive(index) {
        return index == this.$root.$data.sidebarSelectedIndex;
      },

      setSelected(index) {
        this.$root.$data.sidebarSelectedIndex = index;
        this.$root.$data.sidebarSelectedItem = this.listItems[index];
        this.$emit('update:sidebarSelectedItem', this.listItems[index]);
      }
    }
  }
</script>

<style scoped>
  #sidebar {
    height:100%;

    background-color: #F5F5F5;
    border-right: 1px solid black;
  }
</style>



