<template>
  <div id="page2">
    <h2>{{heading}}</h2>
    <p>{{description}}</p>
    <div class="input-group">
      <input v-model="inputText" placeholder="two-way binding example">
    </div>
    <p>Message is: {{ inputText }}</p>
  </div>
</template>

<script>
  export default {
    name: 'Page2',
    data () {
      return {
        heading: 'this is page2',
        description: 'this page has no sidebar, but still scrolls independently. In addition, the two way binding example, also updates the nav component with it\'s value',
        inputText: ''
      }
    },

    watch: {
      // when a local property value changes, do something (in this case update the shared data to display the change in a different component)
      // format: propertyName(changedValue)
      inputText(changedInputText) {
        this.$root.$data.page2InputText = changedInputText;
      }
    }
  }
</script>

<style scoped>
  input {
    margin: 0 auto;
    width:200px;
  }
  #page2 {
    text-align: center;
  }
</style>



