<template>
  <div class="page1-container">
    <div class="page1-sidebar">
      <sidebar></sidebar>
    </div>
    <div class="page1">
      <h2>{{heading}} | {{this.$root.$data.sidebarSelectedItem}}</h2>
      <p>{{description}}</p>
      <h4>Http Get Example</h4>
      <p>Random quote retrieved when the component is created (only show div if we have a quote)</p>
      <p v-if="quote"><i>{{quote}}</i></p>
    </div>
  </div>
</template>

<script>
  import Sidebar from './Sidebar'
  export default {
    name: 'Page1',
    data () {
      return {
        heading: 'this is page1 (home)',
        description: 'this page contains a sidebar, and each will scroll independently, leaving the header and footer static',
        quote:'',
        sidebarSelected:''
      }
    },
    methods : {
      retrieveRandomQuote() {
        let url = "https://talaikis.com/api/quotes/random/";
        this.$http.get(url)
          .then(function(response) {
            console.log(response);
            this.quote = response.body.quote;
          });
      }
    },
    created: function() {
       this.retrieveRandomQuote();
    },

    components: {
      sidebar: Sidebar
    }
  }
</script>

<style scoped>
  .page1-container {
    display: flex;
    flex-direction: row;
    height:100%;
  }
  .page1-sidebar {
    flex:1;
    overflow: auto;
    padding-left:5px;
    min-width:200px;
    max-width:300px;
  }

  .page1 {
    flex:4;
    text-align: center;
  }
</style>



